h1,
p {
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  text-align: center;
}

button {
  background-color: #5c94ce;
  border-radius: 15px;
  color: white;
  padding: 15px 32px;
  margin: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}

.flex-container {
  display: flex;
  justify-content: center;
}

table {
  border: 2px solid #354a5f;
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

th {
  border-bottom: 2px solid #354a5f;
  padding: 8px;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #354a5f;
  color: white;
}
tr:nth-child(even) {
  background-color: #ddd;
}
tr:hover {
  background-color: #cacacb;
}

td {
  text-align: left;
  padding: 8px;
}

h1 {
  padding: 50px;
  text-align: center;
  background: #5c94ce;
  color: white;
  font-size: 60px;
}

.active {
  background-color: green;
}

.italic {
  font-style: italic;
}

.satCount {
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-weight: bold;
  text-align: left;
  padding: 8px;
}

.satCountTable {
  border: 2px solid #354a5f;
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 25%;
}

.space-debris {
  background-color: red;
}
.space-debris:nth-child(even) {
  background-color: red;
}
.space-debris:hover {
  background-color: darkred;
}
